export const fetchPizzas = (category, sortBy) => (dispatch) => {
    dispatch(setLoaded(false));
    console.log("Category:", category, "SortBy:", sortBy);

    // Формирование запроса
    const categoryQuery = category != null ? `category=${category}` : '';
    const sortQuery = `_sort=${sortBy.type}&_order=${sortBy.order}`;

    const query = `${categoryQuery}&${sortQuery}`.replace(/^&/, ''); // удаляем лишний амперсанд в начале строки
    console.log("Generated query:", query); // Добавлен лог для запроса

    // Отправка запроса
    axios.get(`https://my-json-server.typicode.com/daulet1232/pizza.db/pizzas?${query}`)
    .then(({ data }) => {
        console.log("Data from server:", data);
        dispatch(setPizzas(data));
    })
    .catch((error) => {
        console.error("Error fetching pizzas:", error);
    });


};

export const setPizzas = (items) => ({
    type: 'SET_PIZZAS',
    payload: items
})